import { Map, List } from 'immutable';
import mergeDeepOverwriteLists from './utils/mergeDeepOverwriteLists';
import getNtxCss, { transformCssDataInClass } from './utils/ntxCss';
import { transformTextDataInComponent } from './utils/ntxText';

function isMasterId(id) {
  return !!(id && id.charAt && id.charAt(0) === '@');
}

function getStyleTypeById(state, id) {
  return state.reduce((m, v, k) => (v.has(id) ? k : m), null);
}

export function getInstance(instances, masters, id) {
  if (!isMasterId(id)) {
    return instances.get(id, Map());
  }
  const [, masterId, masterInstance] = id.split('@');
  return masters.getIn([masterId, 'instances', masterInstance], Map());
}

export function getMergeData(id, instance, styles) {
  const empty = Map();
  const instanceType = instance.get('type');
  const instanceStyle = instance.get('style');
  const instanceData = instance.get('data', empty);
  const styleData = styles.getIn([instanceType, instanceStyle, 'data']);
  const data = styleData || empty;
  const newMerge = mergeDeepOverwriteLists(data, instanceData);
  return newMerge
    .set('type', instanceType)
    .set('id', id);
}

export function getTransformColStylesToColsClasses(instance, styles) {
  if (instance.get('type') === 'Row') {
    const colsSize = instance.get('colsSize');
    const colsStyles = instance.get('colsStyles');
    const colsClasses = colsSize.reduce(
      (memo, value, index) => {
        const styleName = colsStyles.get(index, 'columnDefault');
        const styleType = getStyleTypeById(styles, styleName);
        if (!styleType) {
          return memo.push({});
        }
        const styleValue = styles.getIn([styleType, styleName, 'data', 'css'], Map());
        return memo.push(transformCssDataInClass(styleValue.toJS()));
      },
      List(),
    );
    return instance.set('colsClasses', colsClasses);
  }
  return instance;
}

export function getTransformColStylesToColsClasses2(instance, styles) {
  if (instance.get('type') === 'Row') {
    const colsSize = instance.get('colsSize');
    const colsStyles = instance.get('colsStyles');
    const colsClasses = colsSize.reduce(
      (memo, value, index) => {
        const styleName = colsStyles.get(index, 'columnDefault');
        const styleValue = styles.getIn([styleName, 'data', 'css'], Map());
        return memo.push(transformCssDataInClass(styleValue.toJS()));
      },
      List(),
    );
    return instance.set('colsClasses', colsClasses);
  }
  return instance;
}

export function getDisplayFlex(instance) {
  return instance.updateIn(['css'], (value) => {
    const css = value || Map({ component: Map() });
    const display = css.getIn(['component', 'display']);
    if (!display || display === 'block') {
      return css
        .setIn(['component', 'display'], 'flex')
        .setIn(['component', 'flexDirection'], 'column')
        .updateIn(['component', 'width'], w => w || List([100, '%']));
    }
    return css;
  });
}

const getUrl = (baseUrl, { origin, id }) => `${baseUrl}${origin}/${id}`;

const transformCssResources = (css, baseUrl) => {
  const { resources = {}, ...rest } = css;
  const transformResources = Object.keys(resources).reduce((m, k) => ({ ...m, [k]: `url(${getUrl(baseUrl, resources[k])})` }), {});
  return { ...rest, ...transformResources };
};

export function getTransformCssToClass(instance) {
  const { css, editorCssProperties, ...rest } = instance.toJS();
  const newCss = Object.keys(css || {}).reduce((m, k) => (
    { ...m, [k]: transformCssResources(css[k], process.env.REACT_APP_URL_RESOURCES) }), {});

  return {
    ...rest,
    classes: transformCssDataInClass(newCss),
    classBorder: getNtxCss(editorCssProperties),
  };
}

export function getData(instance) {
  const { resources = {}, ...rest } = instance;
  const data = {
    ...rest,
    text: transformTextDataInComponent(instance.text),
  };
  const transformResources = Object.keys(resources)
    .reduce(
      (m, k) => ({
        ...m,
        [k]: getUrl(process.env.REACT_APP_URL_RESOURCES, resources[k]),
        [`${k}Origin`]: resources[k].origin,
        [`${k}Ratio`]: resources[k].ratio,
      }),
      {},
    );
  return { ...data, ...transformResources };
}


export function getTransformCssEditor(_instance) {
  const size100Percent = List(['100', '%']);

  const aMargins = [
    'margin',
    'marginTop',
    'marginRight',
    'marginBottom',
    'marginLeft',
    'width',
    'minWidth',
    'maxWidth',
    'alignSelf',
    'justifySelf',
  ];

  const margin0 = List([0]);
  const cssComponent = _instance.getIn(['css', 'component'], Map());
  const cssComponentFiltered = cssComponent.filter((value, key) => (aMargins.includes(key) || key === 'height'));
  const deleteMargins = aMargins.reduce(
    (memo, k) => memo.deleteIn(['css', 'component', k]),
    _instance,
  );
  const deleteMarginsApply = deleteMargins
    .setIn(['css', 'component', 'margin'], margin0)
    .setIn(['css', 'component', 'width'], size100Percent)
    .set('editorCssProperties', cssComponentFiltered);
  return deleteMarginsApply;
}

export function transform(id, data) {
  const {
    instances,
    styles,
    masters,
  } = data;
  const instance = getInstance(instances, masters, id);
  const mergeData = getMergeData(id, instance, styles);
  const transformColStylesToRow = getTransformColStylesToColsClasses(mergeData, styles);
  const transformCssEditor = getTransformCssEditor(transformColStylesToRow);
  const transformCssToClass = getTransformCssToClass(transformCssEditor);
  return getData(transformCssToClass);
}
