import React from 'react';
import PropTypes from 'prop-types';
import { generateClass } from '../utils/ntxCss';
import components from '../index';
import ChildsContainer from '../utils/ChildsContainer';

const classRelative = generateClass({
  position: 'relative',
  width: '100%',
});

const Render2BaseComp = (props) => {
  const {
    index,
    data,
    children,
  } = props;
  const { classBorder, classes, type } = data;
  const Comp = components[type];
  if (!Comp) {
    return null;
  }
  return (
    <div className={`${classBorder || ''} ${classRelative}`}>
      <Comp {...data} index={index}>
        <ChildsContainer classes={classes}>
          {children}
        </ChildsContainer>
      </Comp>
    </div>
  );
};

Render2BaseComp.propTypes = {
  id: PropTypes.string,
  index: PropTypes.number,
  data: PropTypes.objectOf(PropTypes.oneOfType(
    [
      PropTypes.array,
      PropTypes.string,
      PropTypes.object,
      PropTypes.number,
      PropTypes.bool,
      PropTypes.func],
  )),
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

Render2BaseComp.defaultProps = {
  id: null,
  index: null,
  data: {},
  children: null,
};

export default Render2BaseComp;
