import React from 'react';
import PropTypes from 'prop-types';
import { generateClass } from '../utils/ntxCss';

const classDefault = generateClass({
  border: 'none',
});

function ButtonActivityCheck(props) {
  const {
    classes,
    title,
    onClick,
    children,
  } = props;
  const { component } = classes;
  return (
    <button type="button" className={`${classDefault} ${component}`} title={title} onClick={onClick}>
      {children}
    </button>
  );
}

ButtonActivityCheck.propTypes = {
  title: PropTypes.string,
  classes: PropTypes.objectOf(PropTypes.string),
  onClick: PropTypes.func,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};
ButtonActivityCheck.defaultProps = {
  title: 'Check',
  classes: {
    component: '',
  },
  onClick: () => null,
  children: null,
};
export default ButtonActivityCheck;
