import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { fromJS } from 'immutable';
import { useDispatch } from 'react-redux';
import setPageAction from './actions/setPageAction';
import MenuOslo from './players/MenuOslo';
import RenderInteractive from './RenderInteractive/RenderInteractive';

function App() {
  const [ntxData, setNtxData] = useState(undefined);
  const { unitId = 'unit1' } = useParams();
  const dispatch = useDispatch();
  const setInitialNtxData = (newData) => {
    dispatch(setPageAction(newData.structure.getIn(['structureRoot', 0])));
    setNtxData(newData);
  };
  useEffect(() => {
    if (!window.ntxData) {
      fetch(`${process.env.REACT_APP_URL_LOADRENDER}${unitId}`)
        .then((response) => {
          if (!response.ok) {
            throw Error(response.statusText);
          }
          return response;
        })
        .then(response => response.json())
        .then((data) => {
          const initialData = Object.keys(data)
            .reduce((memo, k) => ({ ...memo, [k]: fromJS(data[k]) }), {});
          setInitialNtxData(initialData);
        });
    } else {
      const initialData = Object.keys(window.ntxData)
        .reduce((memo, k) => ({ ...memo, [k]: fromJS(window.ntxData[k]) }), {});
      setInitialNtxData(initialData);
    }
  }, []);
  if (!ntxData) {
    return <p>Loading</p>;
  }
  return (
    <>
      <MenuOslo ntxData={ntxData} />
      <RenderInteractive ntxData={ntxData} />
    </>
  );
}

export default App;
