import React, { useLayoutEffect } from 'react';
import PropTypes from 'prop-types';
import Render2BaseComp from '../components/Render2/Render2BaseComp';

const RenderPageContent = (props) => {
  const {
    id,
    index,
    data,
    children,
  } = props;

  useLayoutEffect(() => {
    window.scroll({
      top: 0,
    });
  }, [id]);

  return (
    <Render2BaseComp data={data} index={index}>
      {children}
    </Render2BaseComp>
  );
};

RenderPageContent.propTypes = {
  id: PropTypes.string,
  index: PropTypes.number,
  data: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.array, PropTypes.string, PropTypes.object, PropTypes.number, PropTypes.bool],
  )),
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

RenderPageContent.defaultProps = {
  id: null,
  index: null,
  data: {},
  children: null,
};

export default RenderPageContent;
