import React from 'react';
import PropTypes from 'prop-types';
import useSingleChoiceActivityMode from '../hooks/useSingleChoiceActivityMode';
import Render2BaseComp from '../components/Render2/Render2BaseComp';

const RenderFeedbackNegative = (props) => {
  const {
    id,
    index,
    data,
    children,
    ntxData,
  } = props;
  const mode = useSingleChoiceActivityMode(id, ntxData);
  if (mode !== 'incorrect') {
    return null;
  }
  return (
    <Render2BaseComp data={data} index={index}>
      {children}
    </Render2BaseComp>
  );
};

RenderFeedbackNegative.propTypes = {
  id: PropTypes.string,
  index: PropTypes.number,
  data: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.array, PropTypes.string, PropTypes.object, PropTypes.number, PropTypes.bool],
  )),
  ntxData: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.array, PropTypes.string, PropTypes.object, PropTypes.number, PropTypes.bool],
  )),
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

RenderFeedbackNegative.defaultProps = {
  id: null,
  index: null,
  data: {},
  ntxData: {},
  children: null,
};

export default RenderFeedbackNegative;
