import { SET_DATA, SET_PAGE } from '../actions/types';

const initialState = {
  page: null,
  oldPage: null,
};

const setPage = (state, { payload = {} }) => {
  const oldPage = state.page;
  const { page } = payload;
  return { ...state, page, oldPage };
};

const setData = (state, { payload = {} }) => {
  const { id, prop, value } = payload;
  const key = `${id}__${prop}`;
  return { ...state, [key]: value };
};

export default (state = initialState, action = {}) => {
  const { type } = action;
  switch (type) {
    case SET_PAGE:
      return setPage(state, action);
    case SET_DATA:
      return setData(state, action);
    default:
      return state;
  }
};
