import { Map } from 'immutable';
import { isMasterId, getMasterId, getMasterInstanceId } from '../components/Render2/isMasterId';

export default (id, ntxData) => {
  const {
    instances,
    masters,
  } = ntxData;
  if (!isMasterId(id)) {
    return instances.get(id, Map());
  }
  const masterId = getMasterId(id);
  const masterInstance = getMasterInstanceId(id);
  return masters.getIn([masterId, 'instances', masterInstance], Map());
};
