import React from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import useAscendantActivitySingleChoiceId from '../hooks/useAscendantActivitySingleChoiceId';
import useSingleChoiceActivityChecked from '../hooks/useSingleChoiceActivityChecked';
import setDataAction from '../actions/setDataAction';
import Render2BaseComp from '../components/Render2/Render2BaseComp';

const RenderSingleChoiceOption = (props) => {
  const {
    id,
    index,
    data,
    children,
    ntxData,
  } = props;
  const dispatch = useDispatch();
  const singleChoiceActivityId = useAscendantActivitySingleChoiceId(id, ntxData);
  const onChange = () => {
    dispatch(setDataAction(singleChoiceActivityId, 'response', id));
  };
  const checked = useSingleChoiceActivityChecked(id, ntxData);
  const passedData = { ...data, onChange, checked };
  return (
    <Render2BaseComp data={passedData} index={index}>
      {children}
    </Render2BaseComp>
  );
};

RenderSingleChoiceOption.propTypes = {
  id: PropTypes.string,
  index: PropTypes.number,
  data: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.array, PropTypes.string, PropTypes.object, PropTypes.number, PropTypes.bool],
  )),
  ntxData: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.array, PropTypes.string, PropTypes.object, PropTypes.number, PropTypes.bool],
  )),
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

RenderSingleChoiceOption.defaultProps = {
  id: null,
  index: null,
  data: {},
  ntxData: {},
  children: null,
};

export default RenderSingleChoiceOption;
