
import { useSelector } from 'react-redux';
import { List } from 'immutable';
import getType from '../dataFunc/getType';
import { getData } from './useData';

export const getAscendantActivitySingleChoiceId = (id, ntxData, state) => {
  const ascendants = getData(id, 'ascendants', state) || List();
  return ascendants.find(
    currentId => getType(currentId, ntxData) === 'ActivitySingleChoice',
  );
};

export default (id, ntxData) => useSelector(
  state => getAscendantActivitySingleChoiceId(id, ntxData, state),
);
