import { css, cx, injectGlobal } from 'emotion';

function getString(key, value, unit = '', extra = []) {
  const extraParams = extra.reduce((m, v) => `${m} ${v}`, '');
  return { [key]: `${value}${unit}${extraParams}` };
}


const descriptor = {
  marginTop: 'array',
  marginRight: 'array',
  marginBottom: 'array',
  marginLeft: 'array',
  paddingTop: 'array',
  paddingRight: 'array',
  paddingBottom: 'array',
  paddingLeft: 'array',
  borderTop: 'array',
  borderRight: 'array',
  borderBottom: 'array',
  borderLeft: 'array',
  borderTopLeftRadius: 'array',
  borderTopRightRadius: 'array',
  borderBottomRightRadius: 'array',
  borderBottomLeftRadius: 'array',
  borderWidth: 'array',
  minHeight: 'array',
  maxHeight: 'array',
  height: 'array',
  minWidth: 'array',
  maxWidth: 'array',
  width: 'array',
  overflowX: 'string',
  overflowY: 'string',
  boxShadow: 'string',
  opacity: 'string',
  visibility: 'string',
  outline: 'array',
  objectFit: 'string',
  backgroundColor: 'string',
  backgroundImage: 'string',
  backgroundPosition: 'string',
  backgroundSize: 'string',
  backgroundRepeat: 'string',
  backgroundOrigin: 'string',
  backgroundClip: 'string',
  backgroundAttachment: 'string',
  fontStyle: 'string',
  fontVariant: 'string',
  fontWeight: 'string',
  fontSize: 'array',
  fontFamily: 'string',
  display: 'string',
  gridTemplateColumns: 'string',
  gridTemplateRows: 'string',
  gridTemplateAreas: 'string',
  gridColumn: 'string',
  gridArea: 'string',

  justifyItems: 'string',
  alignItems: 'string',
  justifyContent: 'string',
  alignContent: 'string',

  justifySelf: 'string',
  alignSelf: 'string',

  textAlign: 'string',
  textDecoration: 'string',
  fill: 'string',
  stroke: 'string',
  selectors: 'selectors',
  border: 'topRightBottomLeft',
  padding: 'topRightBottomLeft',
  margin: 'topRightBottomLeft',
  content: 'string',
  position: 'string',
  transform: 'string',
  top: 'array',
  bottom: 'array',
  right: 'array',
  left: 'array',
  zIndex: 'string',
  listStyle: 'string',
  cursor: 'string',
  verticalAlign: 'string',
  lineHeight: 'array',
  color: 'string',
  pointerEvents: 'string',
  transition: 'string',
  strokeDashoffset: 'string',
  boxSizing: 'string',
  whiteSpace: 'string',
  textOverflow: 'string',
  float: 'string',
  flexDirection: 'string',
  textTransform: 'string',
  fontSrc: 'fontFace',
};

const descriptorKeys = Object.keys(descriptor);

function getCssData(cssData) {
  const operations = {
    array: (key, val) => {
      const [value, unit, ...extra] = val;
      return getString(key, value, unit, extra);
    },
    topRightBottomLeft: (key, val) => {
      const composeObj = ['Top', 'Right', 'Bottom', 'Left'].reduce(
        (memo, value) => ({
          ...memo,
          [`${key}${value}`]: val,
        }),
        {},
      );
      return getCssData(composeObj);
    },
    string: (key, val) => getString(key, val),
    selectors: (key, val) => val.reduce((memo, value) => {
      const [selector, data] = value;
      return { ...memo, [selector]: getCssData(data) };
    }, {}),
    fontFace: (key, val, all) => {
      const fontFace = `
        @font-face {
          font-family: ${all.fontFamily};
          font-style: ${all.fontStyle};
          font-weight: ${all.fontWeight};
          src: url(${val})
        }
      `;
      injectGlobal(fontFace);
      return {};
    },
  };
  return Object.keys(cssData || {})
    .filter(k => descriptorKeys.includes(k) || k.includes('font'))
    .reduce((m, k) => {
      const operation = operations[descriptor[k]];
      return operation ? { ...m, ...operation(k, cssData[k], cssData) } : m;
    }, {});
}

export default function getNtxCss(data) {
  const ret = getCssData(data);
  return Object.keys(ret).length ? css(ret) : '';
}

export function transformCssDataInClass(data) {
  return Object.keys(data || {}).reduce(
    (m, k) => ({ ...m, [k]: getNtxCss(data[k]) }),
    {},
  );
}

export function generateClass(data) {
  return css(data);
}

export function composeClasses(...args) {
  return cx(args);
}
