import setDataAction from '../actions/setDataAction';
import getCorrect from '../dataFunc/getCorrect';
import { getData } from '../hooks/useData';
import getDescendantsByType from '../dataFunc/getDescendantsByType';

export default (id, ntxData) => (dispatch, getState) => {
  const state = getState();
  const response = getData(id, 'response', state);
  const optionIds = getDescendantsByType(id, 'SingleChoiceOption', ntxData);
  const correctOption = optionIds.find(optionId => getCorrect(optionId, ntxData));
  const mode = response === correctOption ? 'correct' : 'incorrect';
  dispatch(setDataAction(id, 'mode', mode));
};
