import React from 'react';
import PropTypes from 'prop-types';
import { List } from 'immutable';
import { transform } from '../transforms';
import { isMasterId, getMasterId, getMasterInstanceId } from './isMasterId';
import { generateClass } from '../utils/ntxCss';

const classRoot = generateClass({
  display: 'grid',
});

function Render2(props) {
  const {
    id, getComp, ntxData, onChild,
  } = props;

  const {
    instances,
    masters,
  } = ntxData;

  const descendants = (() => {
    const getType = (typeId) => {
      if (isMasterId(typeId)) {
        const masterId = getMasterId(typeId);
        const masterInstanceId = getMasterInstanceId(typeId);
        return masters.getIn([masterId, 'instances', masterInstanceId, 'type'], '');
      }
      return instances.getIn([typeId, 'type'], '');
    };

    const getChilds = (parentId) => {
      if (isMasterId(parentId)) {
        const masterId = getMasterId(parentId);
        const masterInstanceId = getMasterInstanceId(parentId);
        return [...masters.getIn([masterId, 'instances', masterInstanceId, 'data', 'childs'], List())];
      }
      return [...instances.getIn([parentId, 'data', 'childs'], List())];
    };

    const getChildren = (parentId, acc) => getChilds(parentId).reduce(
      (memo, child) => ({
        ...memo,
        [child]: `${getType(child)}||${getChilds(child)}`,
        ...getChildren(child),
      }),
      acc,
    );
    return getChildren(id, { [id]: `${getType(id)}||${getChilds(id)}` });
  })();

  const getData = (dataId) => {
    const [type, strChilds] = (descendants[dataId] || '').split('||');
    return {
      type,
      childs: strChilds ? strChilds.split(',') : [],
    };
  };

  const getChildren = (passedChilds, ascendants) => passedChilds.map(
    (child, index) => {
      const { type, childs } = getData(child);
      const Comp = getComp(type, ascendants.count(), child);
      onChild(child, ascendants);
      return (
        <Comp
          id={child}
          index={index}
          key={child}
          ntxData={ntxData}
          data={transform(child, ntxData)}
        >
          {getChildren(childs, ascendants.unshift(child))}
        </Comp>
      );
    },
  );
  const { type, childs } = getData(id);

  const Comp = getComp(type, 0);
  onChild(id, List());
  return (
    <div className={classRoot}>
      <Comp
        id={id}
        index={0}
        ntxData={ntxData}
        data={transform(id, ntxData)}
      >
        {getChildren(childs, List([id]))}
      </Comp>
    </div>
  );
}

Render2.propTypes = {
  id: PropTypes.string,
  getComp: PropTypes.func,
  ntxData: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.array, PropTypes.string, PropTypes.object, PropTypes.number],
  )),
  onChild: PropTypes.func,
};

Render2.defaultProps = {
  id: null,
  getComp: () => null,
  ntxData: {},
  onChild: () => null,
};

export default Render2;
