import React from 'react';
import PropTypes from 'prop-types';
import { generateClass } from '../utils/ntxCss';
import { ReactComponent as IconCheckCircle } from './iconCheckCircle24x24.svg';

const classContainerValues = generateClass({
  display: 'flex',
  label: {
    display: 'block',
    position: 'relative',
    paddingLeft: '1.5rem',
    cursor: 'pointer',
    userSelect: 'none',
  },
  input: {
    position: 'absolute',
    opacity: 0,
    cursor: 'pointer',
    height: 0,
    width: 0,
    margin: 0,
    '~ span': {
      position: 'absolute',
      top: 0,
      left: 0,
      height: '1.5rem',
      width: '1.5rem',
      border: '1px solid #1A1A1A',
      borderRadius: '50%',
      '.singlechoice-check': {
        display: 'none',
      },
    },
    ':checked ~ span': {
      border: 'none',
      '.singlechoice-check': {
        display: 'block',
      },
    },
  },
});
const classContainerChildren = generateClass({
  width: 'calc(100% - 1.5rem)',
});

function SingleChoiceOption(props) {
  const {
    id,
    children,
    classes,
    onChange,
    checked,
  } = props;
  const { component } = classes;
  return (
    <section data-component="SingleChoiceOption" className={component}>
      <div className={classContainerValues}>
        <label onClick={e => e.stopPropagation()} htmlFor={`htmlFor_${id}`}>
          <input id={`htmlFor_${id}`} type="radio" onChange={onChange} checked={checked} />
          <span>
            <IconCheckCircle className="singlechoice-check" />
          </span>
        </label>
        <div className={classContainerChildren}>
          {children}
        </div>
      </div>
    </section>
  );
}

SingleChoiceOption.propTypes = {
  id: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  classes: PropTypes.objectOf(PropTypes.string),
  onChange: PropTypes.func,
  checked: PropTypes.bool,
};
SingleChoiceOption.defaultProps = {
  id: null,
  children: null,
  classes: {
    component: '',
  },
  onChange: () => null,
  checked: false,
};
export default SingleChoiceOption;
