export function isMasterId(id) {
  return !!(id && id.charAt && id.charAt(0) === '@');
}

export function getMasterUniqueId(id) {
  if (!isMasterId(id)) {
    return null;
  }
  const [, , , masterUniqueId] = id.split('@');
  return masterUniqueId;
}

export function getMasterInstanceId(id) {
  if (!isMasterId(id)) {
    return null;
  }
  const [, , masterInstance] = id.split('@');
  return masterInstance;
}

export function getMasterId(id) {
  if (!isMasterId(id)) {
    return null;
  }
  const [, masterId] = id.split('@');
  return masterId;
}
