import React from 'react';
import PropTypes from 'prop-types';
import { generateClass } from '../utils/ntxCss';

const classDefault = generateClass({
  position: 'absolute',
  top: 0,
  left: 0,
  width: '100%',
  height: '100%',
});
const classVideoDefault = generateClass({
  position: 'relative',
  height: 0,
  overflow: 'hidden',
});

function Video(props) {
  const {
    src,
    alt,
    title,
    width,
    height,
    classes,
    srcOrigin,
  } = props;
  const {
    component,
  } = classes;
  const classVideoSize = generateClass({
    paddingBottom: `${((height / width) * 100)}%`,
  });
  return (
    <figure className={component}>
      <div className={`${classVideoDefault} ${classVideoSize}`}>
        {srcOrigin === 'youtube' && (
          <iframe
            title={alt}
            width={width}
            height={height}
            key={src}
            src={src}
            frameBorder="0"
            allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
            className={classDefault}
          />
        )}
        {srcOrigin === 'vimeo' && (
          <iframe
            title={alt}
            key={src}
            src={src}
            width="640"
            height="360"
            frameBorder="0"
            allowFullScreen
            className={classDefault}
          />
        )}
        {srcOrigin === 'ccr' && (
          <video
            width={width}
            height={height}
            controls
            alt={alt}
            title={alt}
            className={classDefault}
            draggable="false"
            key={src}
          >
            <source src={src} type="video/mp4" />
          </video>
        )}
      </div>
    </figure>
  );
}

Video.propTypes = {
  src: PropTypes.string,
  alt: PropTypes.string,
  title: PropTypes.string,
  width: PropTypes.number,
  height: PropTypes.number,
  classes: PropTypes.objectOf(PropTypes.string),
  srcOrigin: PropTypes.string,
};

Video.defaultProps = {
  src: '',
  alt: 'Alt',
  title: 'Video',
  width: 640,
  height: 360,
  classes: {},
  srcOrigin: 'ccr',
};

export default Video;
