import setPageAction from '../actions/setPageAction';
import selectorPage from '../selectors/selectorPage';

export default ntxData => (dispatch, getState) => {
  const state = getState();
  const currentPage = selectorPage(state);
  const pagesContent = [...ntxData.structure.get('structureRoot').values()];
  const currentIndex = pagesContent.indexOf(currentPage);
  const newIndex = Math.min(pagesContent.length - 1, currentIndex + 1);
  dispatch(setPageAction(pagesContent[newIndex]));
};
