import React from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import { generateClass } from '../components/utils/ntxCss';

const classCurrent = generateClass({
  opacity: '1 !important',
});

const MenuOsloItem = (props) => {
  const { id, label, onClick } = props;
  const page = useSelector(state => state.page);
  const objClassName = page === id ? { className: classCurrent } : {};
  const handleClick = () => {
    onClick(id);
  };
  return (
    <button {...objClassName} type="button" onClick={handleClick}>
      <span>{label}</span>
    </button>
  );
};
MenuOsloItem.propTypes = {
  id: PropTypes.string,
  label: PropTypes.string,
  onClick: PropTypes.func,
};
MenuOsloItem.defaultProps = {
  id: null,
  label: '',
  onClick: () => null,
};

export default MenuOsloItem;
