import React from 'react';
import PropTypes from 'prop-types';
import { generateClass } from './ntxCss';

const classDefault = generateClass({
  display: 'grid',
  height: '100%',
  width: '100%',
  alignItems: 'start',
  justifyItems: 'start',
});

function ChildsContainer(props) {
  const {
    children,
    classes,
  } = props;
  const { childsContainer } = classes;
  return (
    <div className={`${classDefault} ${childsContainer || ''}`}>
      {children}
    </div>
  );
}

ChildsContainer.propTypes = {
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]),
  classes: PropTypes.objectOf(PropTypes.string),
};

ChildsContainer.defaultProps = {
  children: null,
  classes: {
    childsContainer: '',
  },
};

export default ChildsContainer;
