import React from 'react';
import PropTypes from 'prop-types';

function SingleChoiceGroupOption(props) {
  const {
    children,
    classes,
  } = props;
  const { component } = classes;
  return (
    <section data-component="SingleChoiceGroupOption" className={component}>
      {children}
    </section>
  );
}

SingleChoiceGroupOption.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  classes: PropTypes.objectOf(PropTypes.string),
};

SingleChoiceGroupOption.defaultProps = {
  children: null,
  classes: {
    component: '',
  },
};

export default SingleChoiceGroupOption;
