import React from 'react';
import PropTypes from 'prop-types';
import { useSelector, useDispatch } from 'react-redux';
import Render2 from '../components/Render2/Render2';
import Render2BaseComp from '../components/Render2/Render2BaseComp';
import RenderFeedbackNegative from './RenderFeedbackNegative';
import RenderFeedbackPositive from './RenderFeedbackPositive';
import RenderSingleChoiceOption from './RenderSingleChoiceOption';
import RenderButtonActivityCheck from './RenderButtonActivityCheck';
import setDataAction from '../actions/setDataAction';
import RenderImage from './RenderImage';
import RenderText from './RenderText';
import RenderVideo from './RenderVideo';
import RenderSection from './RenderSection';
import RenderPageContent from './RenderPageContent';

const getComp = (type, level) => {
  switch (type) {
    case 'FeedbackNegative':
      return RenderFeedbackNegative;
    case 'FeedbackPositive':
      return RenderFeedbackPositive;
    case 'SingleChoiceOption':
      return RenderSingleChoiceOption;
    case 'ButtonActivityCheck':
      return RenderButtonActivityCheck;
    case 'Image':
      return RenderImage;
    case 'Text':
      return RenderText;
    case 'Video':
      return RenderVideo;
    case 'Section':
      return level < 3 ? RenderSection : Render2BaseComp;
    case 'PageContent':
      return RenderPageContent;
    default:
      return Render2BaseComp;
  }
};

const RenderInteractive = (props) => {
  const { ntxData } = props;
  const dispatch = useDispatch();
  const id = useSelector(state => state.page);
  const handleOnChild = (childId, childAscendants) => {
    dispatch(setDataAction(childId, 'ascendants', childAscendants));
  };
  return (
    <Render2 id={id} getComp={getComp} ntxData={ntxData} onChild={handleOnChild} />
  );
};

RenderInteractive.propTypes = {
  ntxData: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.array, PropTypes.string, PropTypes.object, PropTypes.number],
  )),
};

RenderInteractive.defaultProps = {
  ntxData: {},
};

export default RenderInteractive;
