import { useSelector } from 'react-redux';
import { List } from 'immutable';
import getProp from '../dataFunc/getProp';
import getType from '../dataFunc/getType';
import { getData } from './useData';

const getAscendantComplexId = (id, ntxData, state) => {
  const ascendants = getData(id, 'ascendants', state) || List();
  return ascendants.find(
    currentId => !['Section', 'Row'].includes(getType(currentId, ntxData)),
  );
};

const getShouldRender = (id, ntxData, state, defaultValue = true) => {
  const complexId = getAscendantComplexId(id, ntxData, state);
  const type = getType(id, ntxData);
  const ret = getProp(complexId, `render${type}`, ntxData, defaultValue);
  return ret;
};

export default (id, ntxData, defaultValue) => useSelector(
  state => getShouldRender(id, ntxData, state, defaultValue),
);
