import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import posed from 'react-pose';
import goNextPage from '../thunks/goNextPage';
import { generateClass } from '../components/utils/ntxCss';
import setPage from '../actions/setPageAction';
import { ReactComponent as IconMenuToClose36x36 } from './svg/iconMenuToClose36x36.svg';
import { ReactComponent as IconArrowRight36x36 } from './svg/iconArrowRight36x36.svg';
import MenuOsloItem from './MenuOsloItem';

const classOpenCloseButton = generateClass({
  position: 'fixed',
  zIndex: 1003,
  margin: 0,
  padding: 0,
  width: '2.25rem',
  height: '2.25rem',
  border: 'none',
  fontSize: 0,
  color: 'transparent',
  background: '#000',
  cursor: 'pointer',
  ':hover': {
    svg: {
      opacity: '0.5',
    },
  },
});
const classMenuWrap = generateClass({
  position: 'fixed',
  zIndex: 1002,
  width: 'calc(100vw + 3rem)',
  maxWidth: '25.5rem',
  height: '100%',
  background: '#000',
  fontSize: '1.15em',
  button: {
    color: '#FFF',
    ':hover,:focus': {
      opacity: '1',
    },
  },
});
const classMenu = generateClass({
  height: '100%',
  padding: '5.375rem 4.4375rem 0 7.4375rem',
});
const classIconList = generateClass({
  height: '100%',
  overflow: 'auto',
  scrollbarWidth: 'thin',
  scrollbarColor: '#000 transparent',
  '&::-webkit-scrollbar': {
    width: '0.375rem',
    height: '0.375rem',
  },
  '&::-webkit-scrollbar-thumb': {
    backgroundColor: '#000',
  },
  '&:hover': {
    scrollbarColor: '#FFF transparent',
    '&::-webkit-scrollbar-thumb': {
      backgroundColor: '#FFF',
    },
  },
  button: {
    textAlign: 'left',
    cursor: 'pointer',
    border: 'none',
    background: 'transparent',
    display: 'block',
    padding: '0.125rem 0',
    width: '100%',
    marginBottom: '0.625rem',
    opacity: '0.5',
    span: {
      fontFamily: 'SourceSansPro',
      fontWeight: 700,
      fontSize: '1.25rem',
      lineHeight: '1.5625rem',
    },
  },
});

const classOverlay = generateClass({
  backgroundColor: 'transparent',
  position: 'fixed',
  zIndex: 1001,
  cursor: 'pointer',
  top: 0,
  right: 0,
  bottom: 0,
  left: 0,
  width: '100%',
  height: '100%',
  fontSize: 0,
  border: 'none',
});
const classNextButton = generateClass({
  position: 'fixed',
  bottom: 0,
  right: 0,
  zIndex: 1000,
  margin: 0,
  padding: '0',
  width: '2.25rem',
  height: '2.25rem',
  border: 'none',
  fontSize: 0,
  color: 'transparent',
  background: '#000',
  cursor: 'pointer',
  ':hover': {
    svg: {
      opacity: 0.5,
    },
  },
  ':disabled': {
    opacity: 0.5,
    cursor: 'auto',
    ':hover': {
      svg: {
        opacity: 1,
      },
    },
  },
});

const MenuOslo = (props) => {
  const { ntxData } = props;
  const dispatch = useDispatch();
  const AnimatedMenu = posed.div({
    close: {
      x: '-25.5rem',
      transition: {
        duration: 300,
        ease: 'easeOut',
      },
    },
    open: {
      x: '-3rem',
      transition: {
        duration: 300,
        ease: 'easeOut',
      },
    },
  });
  const [menuOpen, setMenuOpen] = useState(undefined);
  const [svgClass, setSvgClass] = useState('');
  const [animationPose, setAnimationPose] = useState('close');
  const [animationInitialPose, setAnimationInitialPose] = useState('close');
  const handleOpenCloseClick = () => {
    if (!menuOpen) {
      setMenuOpen(true);
      setSvgClass('toclose');
      setAnimationPose('open');
      setAnimationInitialPose('close');
    } else {
      setMenuOpen(false);
      setSvgClass('toopen');
      setAnimationPose('close');
      setAnimationInitialPose('open');
    }
  };
  const handleNextClick = () => {
    dispatch(goNextPage(ntxData));
  };
  const handleClickItem = (key) => {
    dispatch(setPage(key));
    handleOpenCloseClick();
  };
  const data = ntxData.structure.get('structureRoot').map(
    (key) => {
      const label = ntxData.instances.getIn([key, 'data', 'title'], 'Page');
      return <MenuOsloItem key={key} id={key} label={label} onClick={handleClickItem} />;
    },
  );
  return (
    <>
      <button
        type="button"
        className={classOpenCloseButton}
        onClick={handleOpenCloseClick}
      >
        <IconMenuToClose36x36 key={svgClass} className={svgClass} />
        Open/Close Menu
      </button>
      <AnimatedMenu
        pose={animationPose}
        initialPose={animationInitialPose}
        className={classMenuWrap}
      >
        <nav className={classMenu}>
          <div className={classIconList}>
            {data}
          </div>
        </nav>
      </AnimatedMenu>
      {menuOpen
        && <button type="button" className={classOverlay} onClick={handleOpenCloseClick}>Close Menu</button>}
      <button
        type="button"
        className={classNextButton}
        onClick={handleNextClick}
      >
        <IconArrowRight36x36 />
        Next
      </button>
    </>
  );
};

MenuOslo.propTypes = {
  ntxData: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.array, PropTypes.string, PropTypes.object, PropTypes.number],
  )),
};
MenuOslo.defaultProps = {
  ntxData: {},
};
export default MenuOslo;
