import React from 'react';
import PropTypes from 'prop-types';
import useShouldRender from '../hooks/useShouldRender';
import Render2BaseComp from '../components/Render2/Render2BaseComp';

const RenderText = (props) => {
  const {
    id,
    index,
    data,
    children,
    ntxData,
  } = props;

  const shouldRender = useShouldRender(id, ntxData);
  if (!shouldRender) return null;
  return (
    <Render2BaseComp data={data} index={index}>
      {children}
    </Render2BaseComp>
  );
};

RenderText.propTypes = {
  id: PropTypes.string,
  index: PropTypes.number,
  data: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.array, PropTypes.string, PropTypes.object, PropTypes.number, PropTypes.bool],
  )),
  ntxData: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.array, PropTypes.string, PropTypes.object, PropTypes.number, PropTypes.bool],
  )),
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

RenderText.defaultProps = {
  id: null,
  index: null,
  data: {},
  ntxData: {},
  children: null,
};

export default RenderText;
