import { useSelector } from 'react-redux';
import { getAscendantActivitySingleChoiceId } from './useAscendantActivitySingleChoiceId';
import { getData } from './useData';

const getSingleChoiceActivityChecked = (id, ntxData, state) => {
  const singleChoiceId = getAscendantActivitySingleChoiceId(id, ntxData, state);
  const response = getData(singleChoiceId, 'response', state);
  return response === id;
};

export default (id, ntxData) => useSelector(
  state => getSingleChoiceActivityChecked(id, ntxData, state),
);
