import React from 'react';
import PropTypes from 'prop-types';
import getAspectRatio from '../utils/getAspectRatio';
import { generateClass } from '../utils/ntxCss';

const classDefault = generateClass({
  overflow: 'hidden',
});
const classFigureDefault = generateClass({
  overflow: 'hidden',
  position: 'relative',
});
const calculatePaddingTop = ratio => generateClass({
  paddingTop: `${ratio}%`,
});

function Image(props) {
  const {
    src,
    alt,
    title,
    classes,
    translateX,
    translateY,
    zoom,
    aspectRatio,
    srcRatio,
    filter,
  } = props;
  const ratio = getAspectRatio(srcRatio, aspectRatio);
  const paddingTop = (() => {
    const [w, h] = ratio.split(':');
    return (+h / +w) * 100;
  })();
  const classImgDefault = generateClass({
    position: 'absolute',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    transform: `scale(${zoom}) translateY(${translateY}) translateX(${translateX})`,
    width: '100%',
    transformOrigin: '0 0',
    margin: 'auto',
  });

  const classFilter = generateClass({ filter });
  const {
    component,
  } = classes;
  return (
    <div data-component="Image" className={`${classDefault} ${component}`}>
      <figure className={`${classFigureDefault} ${calculatePaddingTop(paddingTop)}`}>
        <img
          src={src}
          alt={alt}
          title={alt}
          className={`${classImgDefault} ${classFilter}`}
          draggable="false"
        />
      </figure>
    </div>
  );
}

Image.propTypes = {
  src: PropTypes.string,
  alt: PropTypes.string,
  title: PropTypes.string,
  translateX: PropTypes.string,
  translateY: PropTypes.string,
  zoom: PropTypes.number,
  classes: PropTypes.objectOf(PropTypes.string),
  aspectRatio: PropTypes.string,
  srcRatio: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  filter: PropTypes.string,
};

Image.defaultProps = {
  src: './resources/placeholder.png',
  alt: 'Alt',
  title: 'Image',
  translateX: '0',
  translateY: '0',
  zoom: 1,
  classes: {
    component: '',
  },
  aspectRatio: 'auto',
  srcRatio: '16:9',
  filter: 'none',
};

export default Image;
