import React from 'react';
import PropTypes from 'prop-types';
import 'animate.css/animate.min.css';
import ScrollAnimation from 'react-animate-on-scroll';
import Render2BaseComp from '../components/Render2/Render2BaseComp';

const RenderSection = (props) => {
  const {
    index,
    data,
    children,
  } = props;

  return (

    <Render2BaseComp data={data} index={index}>
      <ScrollAnimation animateIn="fadeIn" style={{ width: '100%' }} animateOnce>
        {children}
      </ScrollAnimation>
    </Render2BaseComp>
  );
};

RenderSection.propTypes = {
  index: PropTypes.number,
  data: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.array, PropTypes.string, PropTypes.object, PropTypes.number, PropTypes.bool],
  )),
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

RenderSection.defaultProps = {
  index: null,
  data: {},
  children: null,
};

export default RenderSection;
