import React from 'react';
import PropTypes from 'prop-types';
import ChildsContainer from '../utils/ChildsContainer';

function getColChildren(children, colChild, totCols, index) {
  if (!colChild) {
    return children;
  }
  return children.filter(
    (child) => {
      const id = child && child.props && child.props.id;
      return (Math.min(colChild[id], totCols - 1) === index
        || child.props.ntxRowAlwaysRender);
    },
  );
}

function Row({
  id,
  children,
  classes,
  colsSize,
  colsChilds,
  extraChild,
  renderEditorSelect,
  colsClasses,
}) {
  const color = renderEditorSelect ? 'gray' : 'transparent';
  const { component } = classes;
  const ExtraChild = extraChild || (() => null);
  const childs = React.Children.toArray(
    (React.Children.toArray(children)[0] || { props: {} }).props.children,
  );
  const gridTemplateColumns = colsSize.reduce((m, v) => {
    if (!v) {
      return m;
    }
    const [value, unit] = v;
    return `${m} ${value || 1}${unit || 'fr'}`;
  }, '');
  const childrenColumns = colsSize.map((colSize, index) => {
    const newChild = getColChildren(childs, colsChilds, colsSize.length, index);
    const keyValue = `${id}_${index}`;
    const currentColsClasses = colsClasses[index];
    const columnClass = (currentColsClasses && currentColsClasses.component) || '';
    return (
      <div
        key={keyValue}
        style={{
          position: 'relative',
          border: `1px dashed ${color}`,
          minHeight: `${newChild.length ? 0 : '50px'}`,
        }}
        className={columnClass}
        data-component="Column"
      >
        <ChildsContainer classes={currentColsClasses}>
          {newChild}
        </ChildsContainer>
        <ExtraChild
          id={id}
          indexColumn={index}
        />
      </div>
    );
  });
  return (
    <div
      data-component="Row"
      className={component}
      style={{
        gridTemplateColumns,
        display: 'grid',
        gridColumnGap: '0',
      }}
    >
      {childrenColumns}
    </div>
  );
}

Row.propTypes = {
  id: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  extraChild: PropTypes.elementType,
  classes: PropTypes.objectOf(PropTypes.string),
  colsChilds: PropTypes.objectOf(PropTypes.number),
  colsSize: PropTypes.arrayOf(PropTypes.array),
  renderEditorSelect: PropTypes.bool,
  colsClasses: PropTypes.arrayOf(PropTypes.object),
};

Row.defaultProps = {
  id: '',
  children: null,
  extraChild: null,
  classes: {
    component: '',
  },
  colsChilds: {},
  colsSize: [[1, 'fr']],
  renderEditorSelect: false,
  colsClasses: [],
};

export default Row;
