import React from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import checkActivitySingleChoice from '../thunks/checkActivitySingleChoice';
import useAscendantActivitySingleChoiceId from '../hooks/useAscendantActivitySingleChoiceId';
import Render2BaseComp from '../components/Render2/Render2BaseComp';

const RenderButtonActivityCheck = (props) => {
  const {
    id,
    index,
    data,
    children,
    ntxData,
  } = props;
  const dispatch = useDispatch();
  const singleChoiceActivityId = useAscendantActivitySingleChoiceId(id, ntxData);
  const onClick = () => {
    dispatch(checkActivitySingleChoice(singleChoiceActivityId, ntxData));
  };
  const passedData = { ...data, onClick };
  return (
    <Render2BaseComp data={passedData} index={index}>
      {children}
    </Render2BaseComp>
  );
};

RenderButtonActivityCheck.propTypes = {
  id: PropTypes.string,
  index: PropTypes.number,
  data: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.array, PropTypes.string, PropTypes.object, PropTypes.number, PropTypes.bool],
  )),
  ntxData: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.array, PropTypes.string, PropTypes.object, PropTypes.number, PropTypes.bool],
  )),
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

RenderButtonActivityCheck.defaultProps = {
  id: null,
  index: null,
  data: {},
  ntxData: {},
  children: null,
};

export default RenderButtonActivityCheck;
