import React from 'react';
import PropTypes from 'prop-types';
import { transformCssDataInClass } from './ntxCss';

function SimpleTextBase(props) {
  const {
    text,
    classes: { component },
  } = props;
  return (
    <div
      className={`${component} ntx-simple-text-base`}
      // eslint-disable-next-line react/no-danger
      dangerouslySetInnerHTML={{ __html: text }}
    />
  );
}
SimpleTextBase.propTypes = {
  text: PropTypes.string,
  classes: PropTypes.objectOf(PropTypes.string),
};
SimpleTextBase.defaultProps = {
  text: '<p>Lorem ipsum dolor sit amet</p>',
  classes: {
    component: '',
  },
};
export default SimpleTextBase;

function GetTextComponent({ text, css }) {
  // esto se hace por que draft envuelve el texto en un div y hardcodea el textalign
  const patchedCss = css && {
    ...css,
    component: {
      ...css.component,
      selectors: (css.component.selectors || []).map((selector) => {
        const [key, value] = selector;
        const patch = [
          '> div',
          {
            textAlign: value.textAlign,
          },
        ];
        const newKey = key === 'p' ? 'p, div' : key;
        return [newKey, {
          ...value, selectors: [...(value.selectors || []), patch],
        }];
      }),
    },
  };
  return <SimpleTextBase text={text} classes={transformCssDataInClass(patchedCss || {})} />;
}
GetTextComponent.propTypes = {
  text: PropTypes.string,
  css: PropTypes.objectOf(PropTypes.string),
};
GetTextComponent.defaultProps = {
  text: '',
  css: {},
};
export function transformTextDataInComponent(data) {
  return Object.keys(data || {}).reduce(
    (m, k) => ({ ...m, [k]: GetTextComponent(data[k]) }),
    {},
  );
}
